<template>
    <div id="bodyWrap">
      <div class="aboutPageHeader">
        <Header/>
        <div class="container">
          <div class="aboutBanner">
            <div class="wrapper">
              <span class="pageTitle">{{t('aboutPage.header.pageTitle')}}</span>
              <div class="info">
                <p>{{t('aboutPage.header.info1')}}<span>{{t('aboutPage.header.info2')}}</span></p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="aboutInfo">
        <div class="container">
          <div class="wrapper">
            <div class="left">
              <div class="titleBlock">
                <span class="title">{{t('aboutPage.aboutInfo.left.title')}}</span>
              </div>
              <div class="contentBlock">
                <div class="textBlock">
                  <p>{{t('aboutPage.aboutInfo.left.text')}}</p>
                </div>
              </div>
            </div>
            <div class="right">
              <div :class="[isActive() ? ['visible','quoteBlock', 'wow', 'animate__animated', 'animate__fadeInUpBig' ] : 'nonVisible']">
                <p :class="[isActive() ? ['visible', 'wow', 'animate__animated', 'animate__fadeIn' ] : 'nonVisible']" data-wow-delay="1s">{{t('aboutPage.aboutInfo.right.quote')}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="joinSection about">
        <div class="container">
          <div class="joinBlock">
            <div class="img dotsBottom">
              <img src="@/assets/img/image_4.jpg" alt="image">
            </div>
            <div class="info">
              <span class="title md">{{t('aboutPage.joinSection.title')}}</span>
              <div class="content">
                <div class="textBlock">
                  <p>{{t('aboutPage.joinSection.text')}}</p>
                </div>
              </div>
              <a href="#" class="btnIconType1 sm">
                            <span class="in">
                                <span class="text">{{t('aboutPage.joinSection.link')}}</span>
                                <span class="icon-arrow"></span>
                            </span>
              </a>
            </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
</template>


<script>
import Footer from '@/components/Footer'
import Header from "@/components/Header";
import {useI18n} from "vue-i18n";

export default {
  components: {Header, Footer },
  name: 'AboutUsPage',
  setup() {
    const {t, locale} = useI18n();
    return {t, locale}
  },
  data: function() {
    return {
      scrollPosition: null,
      screenWidth: null
    };
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll () {
      this.scrollPosition = window.scrollY;
      this.screenWidth = document.scrollingElement.clientWidth;
    },
    isActive() {
      switch(true) {
        case (this.screenWidth < 751):
          return this.scrollPosition > 400;
        case (this.screenWidth < 975):
          return this.scrollPosition > 300;
        case (this.screenWidth < 1183):
          return this.scrollPosition > 300;
        default:
          return this.scrollPosition > 250
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.visible{
  visibility: visible;
  animation-name: fadeInUpBig;
}
.nonVisible{
  visibility: hidden;
  animation-name: none;
}
</style>
